.muhammad-ismaila {
  align-self: stretch;
  position: relative;
  font-weight: 800;
}
.your-name-here {
  width: 426px;
  padding: var(--padding-3xs) 0;
  box-sizing: border-box;
}
.heading,
.your-name-here {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.hi-i-am {
  position: relative;
  letter-spacing: 0.01em;
  line-height: 24px;
  display: inline-block;
  width: 492px;
}
.heading-and-subheading,
.intro-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.intro-text {
  justify-content: center;
  font-size: var(--font-size-sm);
  color: var(--color-darkgray);
  font-family: var(--font-ibm-plex-mono);
}
.heading-and-subheading {
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.lets-get-started,
.vector-icon {
  position: relative;
}
.vector-icon {
  width: 5.89px;
  height: 10px;
}
.call-to-action {
  border-radius: var(--br-9xs);
  background-color: var(--color-green);
  box-shadow: 0 8px 30px rgba(63, 142, 0, 0.5);
  border: 1px solid var(--color-yellowgreen);
  display: flex;
  flex-direction: row;
  padding: 21px 64px;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  font-size: 16px;
  font-family: var(--font-ibm-plex-mono);
}
.header-text {
  width: 538px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-11xl);
}
.photo-here-icon {
  position: relative;
  width: 350px;
  height: 350px;
  object-fit: cover;
}
.header,
.header-text-parent {
  display: flex;
  justify-content: flex-start;
}
.header-text-parent {
  flex-direction: row;
  align-items: center;
  gap: 32px;
}
.header {
  background-color: var(--color-gray-200);
  overflow: hidden;
  flex-direction: column;
  padding: 150px 180px;
  align-items: flex-start;
}
.your-name-here1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.mask-group-icon {
  position: relative;
  width: 445px;
  height: 300px;
  object-fit: cover;
}
.hiptonic-dashboard {
  position: relative;
  font-weight: 800;
}
.work-name-here {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: var(--font-size-5xl);
  font-family: var(--font-raleway);
}
.dashboard-for-an {
  position: relative;
  letter-spacing: 0.01em;
  line-height: 24px;
  display: inline-block;
  width: 421px;
}
.intro-text1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: var(--color-darkgray);
}
.call-to-action1 {
  border-radius: var(--br-9xs);
  background-color: var(--color-green);
  box-shadow: 0 8px 30px rgba(63, 142, 0, 0.3);
  border: 1px solid var(--color-yellowgreen);
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-5xl);
  align-items: center;
  gap: var(--gap-3xs);
  cursor: pointer;
  color: var(--color-white);
}
.call-to-action1,
.work-1,
.work-name-here1 {
  display: flex;
  justify-content: flex-start;
}
.work-1 {
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-8xl);
}
.work-name-here1 {
  flex-direction: row;
  align-items: center;
}
.work-1-parent,
.work-2 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.work-2 {
  flex-direction: column;
  gap: var(--gap-8xl);
  font-size: var(--font-size-5xl);
  font-family: var(--font-raleway);
}
.work-1-parent {
  flex-direction: row;
  gap: var(--gap-11xl);
}
.vector-icon2 {
  position: relative;
  width: 445px;
  height: 318px;
  object-fit: cover;
}
.federal-university-of {
  position: relative;
  font-weight: 800;
  display: flex;
  align-items: flex-end;
  width: 447px;
  flex-shrink: 0;
}
.a-simple-job {
  position: relative;
  letter-spacing: 0.01em;
  line-height: 24px;
  display: inline-block;
  width: 421px;
  flex-shrink: 0;
}
.a-simple-job-application-porta-wrapper {
  padding: var(--padding-3xs);
}
.a-simple-job-application-porta-wrapper,
.frame-parent,
.work-1-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent {
  flex-direction: column;
  gap: 42px;
  text-align: left;
  font-size: var(--font-size-sm);
  font-family: var(--font-ibm-plex-mono);
}
.recent-work,
.testimonial-heading {
  display: flex;
  align-items: center;
  text-align: center;
  font-size: var(--font-size-15xl);
}
.recent-work {
  flex-direction: column;
  padding: 60px 0 58px;
  justify-content: flex-start;
  gap: 29px;
  margin-top: -2px;
  color: var(--color-gray-200);
}
.testimonial-heading {
  flex-direction: row;
  justify-content: center;
  font-family: var(--font-raleway);
}
.placeholder {
  flex: 1;
  position: relative;
  line-height: 21px;
  display: flex;
  align-items: center;
  height: 36px;
}
.email,
.enter-email-placeholder {
  display: flex;
  justify-content: flex-start;
}
.enter-email-placeholder {
  border-radius: var(--br-9xs);
  background-color: var(--color-whitesmoke);
  border: 1px solid var(--color-gainsboro);
  box-sizing: border-box;
  width: 350px;
  height: 40px;
  flex-direction: row;
  padding: 0 var(--padding-xs);
  align-items: center;
  color: var(--color-gray-100);
}
.email {
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-8xs);
}
.mobile {
  position: relative;
  font-weight: 600;
}
.get-in-touch,
.personal-portfolio-template {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.get-in-touch {
  background-color: var(--color-gray-200);
  padding: 81px 465px;
  gap: 21px;
  margin-top: -2px;
  font-size: var(--font-size-xs);
  font-family: var(--font-ibm-plex-mono);
}
.personal-portfolio-template {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: 44px;
  color: var(--color-white);
  font-family: var(--font-raleway);
}
@media screen and (max-width: 960px) {
  .header-text-parent,
  .work-1-parent {
    flex-direction: column;
  }
}
