@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-ibm-plex-mono: "IBM Plex Mono";
  --font-raleway: Raleway;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-15xl: 34px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #757575;
  --color-gray-200: #080808;
  --color-whitesmoke: #f8f8f8;
  --color-gainsboro: #d8d8d8;
  --color-green: #3f8e00;
  --color-yellowgreen: #62ba1b;
  --color-darkgray: #9c9c9c;

  /* Gaps */
  --gap-8xs: 5px;
  --gap-8xl: 27px;
  --gap-3xs: 10px;
  --gap-11xl: 30px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-3xs: 10px;
  --padding-5xl: 24px;

  /* border radiuses */
  --br-9xs: 4px;
}
